import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Divider,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography,
} from "@mui/material";
import { useEffect, useState } from "react";
import { toast } from "react-toastify";
import api from "../../services/api";
import {
  validarCNPJ,
  validarCPF,
  validarDataNascimento,
  validarEmail,
} from "../../services/utils";

const ModalFormClient = ({
  isOpen,
  onClose,
  selectedActivitie,
  setRefresh,
}) => {
  //eslint-disable-next-line
  const [fields, setFields] = useState([]);
  const fieldsWithErrors = selectedActivitie?.columns;
  const [client, setClient] = useState({});

  const [form, setForm] = useState({
    numero: "",
    email: "",
    tipo_pessoa: "",
    whatsapp: "",
    data_nascimento: "",
    cnpj_cpf: "",
    referencia: "",
    contribuinte_icms: "",
  });

  useEffect(() => {
    const getData = async () => {
      try {
        const response = await api.get(`/company_tables/all_tables`);
        setFields(response.data.data[0].columns);
        const responseClient = await api.get(
          `/clients/${selectedActivitie?.record_id}`,
        );
        setClient(responseClient?.data);
        setForm({
          numero: responseClient?.data.numero || "",
          email: responseClient?.data.email || "",
          tipo_pessoa:
            fieldsWithErrors?.findIndex(
              (field) => field.label === "Tipo de Pessoa",
            ) !== -1 && selectedActivitie?.status === 0
              ? ""
              : responseClient?.data.tipo_pessoa,
          whatsapp: responseClient?.data.whatsapp || "",
          data_nascimento: responseClient?.data.data_nascimento || "",
          cnpj_cpf: responseClient?.data.cnpj_cpf || "",
          referencia: responseClient?.data.referencia || "",
          contribuinte_icms:
            fieldsWithErrors?.findIndex(
              (field) => field.label === "Contribuinte de ICMS",
            ) !== -1 && selectedActivitie?.status === 0
              ? ""
              : responseClient?.data.contribuinte_icms,
        });
      } catch (error) {
        console.error("Erro ao carregar os dados dos clientes", error);
      }
    };
    getData();
  }, []);

  const updateUser = async () => {
    const data = {
      numero: form.numero.replace(/\D/g, ""),
      email: form.email,
      tipo_pessoa: form.tipo_pessoa,
      whatsapp: form.whatsapp.replace(/\D/g, ""),
      data_nascimento: form.data_nascimento,
      cnpj_cpf: form.cnpj_cpf.replace(/\D/g, ""),
      referencia: form.referencia,
      contribuinte_icms: form.contribuinte_icms,
    };
    try {
      await api.put(`/clients/${selectedActivitie?.record_id}`, data);

      toast.success("Correo de dados do cliente concluda com sucesso!");

      await api.put(`auditing/${selectedActivitie?.id}/toggle_status`);

      setRefresh((prev) => !prev);
      onClose();
    } catch (error) {
      console.error("Erro ao editar usurio", error);
    }
  };

  const handleSave = () => {
    if (
      !form.numero ||
      !form.email ||
      !form.tipo_pessoa ||
      !form.whatsapp ||
      !form.data_nascimento ||
      !form.cnpj_cpf ||
      !form.referencia ||
      form.contribuinte_icms === ""
    ) {
      toast.warning("Preencha todos os campos obrigatrios");
      return;
    }

    if (!validarEmail(form.email)) {
      toast.warning("E-mail invlido");
      return;
    }

    if (!validarDataNascimento(form.data_nascimento)) {
      toast.warning(
        "Data de nascimento invlida. O cliente deve ser maior de 18 anos.",
      );
      return;
    }

    if (form.tipo_pessoa === "F" && !validarCPF(form?.cnpj_cpf)) {
      toast.warning("CPF invlido");
      return;
    }

    if (form.tipo_pessoa === "J" && !validarCNPJ(form.cnpj_cpf)) {
      toast.warning("CNPJ invlido");
      return;
    }

    updateUser();
  };

  return (
    <Dialog open={isOpen} onClose={onClose}>
      <DialogTitle>
        Formulrio para correo de dados invlidos
        <Typography>ID do Cliente: #{selectedActivitie?.record_id}</Typography>
        <Typography color="textSecondary" fontWeight="normal" fontSize="small">
          Os campos com borda vermelha representam os campos invlidos do
          Cliente
        </Typography>
        <Divider sx={{ borderColor: "gray.300", width: "100%", my: 2 }} />
      </DialogTitle>
      <DialogContent className="grid grid-cols-1 lg:grid-cols-2 gap-4">
        <Box>
          <InputLabel>Nmero da residncia *</InputLabel>
          <TextField
            type="number"
            value={form?.numero}
            placeholder="Digite o Nmero da residncia"
            helperText="Exemplo: 123"
            disabled={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Numero da residncia",
              ) === -1 || selectedActivitie?.status
            }
            error={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Numero da residncia",
              ) !== -1 || selectedActivitie?.status
            }
            onKeyDown={(e) => {
              if (e.key === "-" || e.key === "e" || e.key === "E") {
                e.preventDefault();
              }
            }}
            onChange={(e) => setForm({ ...form, numero: e.target.value })}
            fullWidth
          />
        </Box>
        <Box>
          <InputLabel>Email *</InputLabel>
          <TextField
            type="email"
            value={form.email}
            placeholder="Digite o Email"
            helperText="Exemplo: usuario@dominio.com"
            disabled={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Email",
              ) === -1 || selectedActivitie?.status
            }
            error={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Email",
              ) !== -1 || selectedActivitie?.status
            }
            onChange={(e) => setForm({ ...form, email: e.target.value })}
            fullWidth
          />
        </Box>
        <Box>
          <InputLabel>Tipo de Pessoa *</InputLabel>
          <Select
            value={form.tipo_pessoa}
            disabled={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Tipo de Pessoa",
              ) === -1 || selectedActivitie?.status
            }
            error={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Tipo de Pessoa",
              ) !== -1 || selectedActivitie?.status
            }
            onChange={(e) =>
              setForm({
                ...form,
                tipo_pessoa: e.target.value,
                cnpj_cpf: "",
              })
            }
            fullWidth
          >
            <MenuItem value="F">Pessoa Fsica</MenuItem>
            <MenuItem value="J">Pessoa Jurdica</MenuItem>
          </Select>
        </Box>
        <Box>
          <InputLabel>WhatsApp *</InputLabel>
          <TextField
            type="tel"
            value={form.whatsapp}
            placeholder="Digite o WhatsApp"
            helperText="Exemplo: (99) 99999-9999"
            disabled={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Whatsapp",
              ) === -1 || selectedActivitie?.status
            }
            error={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Whatsapp",
              ) !== -1 || selectedActivitie?.status
            }
            maxLength={15}
            onChange={(e) => {
              const value = e.target.value.replace(/\D/g, "");
              const formatted = value
                .replace(/^(\d{2})(\d)/, "($1) $2")
                .replace(/(\d{5})(\d)/, "$1-$2")
                .slice(0, 15);
              setForm({ ...form, whatsapp: formatted });
            }}
            fullWidth
          />
        </Box>
        <Box>
          <InputLabel>Data de Nascimento *</InputLabel>
          <TextField
            type="date"
            value={form.data_nascimento}
            placeholder="Digite a Data de Nascimento"
            helperText="Exemplo: 01/01/2000"
            disabled={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Data de Nascimento",
              ) === -1 || selectedActivitie?.status
            }
            error={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Data de Nascimento",
              ) !== -1 || selectedActivitie?.status
            }
            onChange={(e) =>
              setForm({ ...form, data_nascimento: e.target.value })
            }
            fullWidth
          />
        </Box>
        <Box>
          <InputLabel>{form.tipo_pessoa === "F" ? "CPF" : "CNPJ"} *</InputLabel>
          <TextField
            type="text"
            value={form.cnpj_cpf}
            placeholder={
              form.tipo_pessoa === "F" ? "Digite o CPF" : "Digite o CNPJ"
            }
            helperText={
              form.tipo_pessoa === "F"
                ? "Exemplo: 000.000.000-00"
                : "Exemplo: 00.000.000/0000-00"
            }
            disabled={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "CPF" || field.column === "CNPJ",
              ) === -1 || selectedActivitie?.status
            }
            error={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "CPF" || field.column === "CNPJ",
              ) !== -1 || selectedActivitie?.status
            }
            maxLength={form.tipo_pessoa === "F" ? 14 : 18}
            onChange={(e) => {
              const value = e.target.value.replace(/\D/g, "");
              let formatted = value;
              if (form.tipo_pessoa === "F") {
                formatted = value
                  .replace(/(\d{3})(\d)/, "$1.$2")
                  .replace(/(\d{3})(\d)/, "$1.$2")
                  .replace(/(\d{3})(\d{1,2})$/, "$1-$2");
              } else {
                formatted = value
                  .replace(/(\d{2})(\d)/, "$1.$2")
                  .replace(/(\d{3})(\d)/, "$1.$2")
                  .replace(/(\d{3})(\d)/, "$1/$2")
                  .replace(/(\d{4})(\d{1,2})$/, "$1-$2");
              }
              setForm({
                ...form,
                cnpj_cpf: formatted.slice(
                  0,
                  form.tipo_pessoa === "F" ? 14 : 18,
                ),
              });
            }}
            fullWidth
          />
        </Box>
        <Box>
          <InputLabel>Referncia do Endereo *</InputLabel>
          <TextField
            type="text"
            value={form.referencia || client?.referencia}
            placeholder="Digite a Referncia do Endereo"
            helperText="Exemplo: Prximo ao mercado"
            disabled={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Referncia do Endereo",
              ) === -1 || selectedActivitie?.status
            }
            error={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Referncia do Endereo",
              ) !== -1 || selectedActivitie?.status
            }
            onChange={(e) => setForm({ ...form, referencia: e.target.value })}
            fullWidth
          />
        </Box>
        <Box>
          <InputLabel>Contribuinte de ICMS *</InputLabel>
          <Select
            value={form?.contribuinte_icms}
            disabled={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Contribuinte de ICMS",
              ) === -1 || selectedActivitie?.status
            }
            error={
              fieldsWithErrors?.findIndex(
                (field) => field.label === "Contribuinte de ICMS",
              ) !== -1 || selectedActivitie?.status
            }
            onChange={(e) =>
              setForm({ ...form, contribuinte_icms: e.target.value })
            }
            fullWidth
          >
            <MenuItem value={0}>No</MenuItem>
            <MenuItem value={1}>Sim</MenuItem>
          </Select>
        </Box>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Voltar</Button>
        <Button
          variant="contained"
          color="primary"
          onClick={handleSave}
          disabled={selectedActivitie?.status}
        >
          Atualizar Dados
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default ModalFormClient;
